if (!runningTests) {
  require("fastboot-inputs/app")["default"].create({"name":"fastboot-inputs","version":"0.0.0+f285e775"});
}

define('~fastboot/app-factory', ['fastboot-inputs/app', 'fastboot-inputs/config/environment'], function(App, config) {
  App = App['default'];
  config = config['default'];

  return {
    'default': function() {
      return App.create(config.APP);
    }
  };
});


